<template>
  <div>
    <div class="container scontainer">
      <div class="search-bar">
        <van-search
          v-model="keywords" 
          show-action
          label="查询"
          placeholder="请输入搜索关键词"
          @search="searchData"  
          clearable
        >
          <template #action>
            <div @click="searchData">搜索</div>
          </template>
        </van-search>
      </div>



      <Empty v-if="list != null && list.length == 0"></Empty>


    </div>
  </div>
</template>

<script>
import { getVisiting } from "@api/public";
import { Icon, Search, Loading, List } from "vant";
import Empty from "@components/Empty";
import Loadings from "@components/Loadings";
export default {
  name: "WaitPerson",
  components: {
    [Icon.name]: Icon,
    [Search.name]: Search,
    [Loading.name]: Loading,
    [List.name]: List,
    Empty,
    Loadings,
  },

  data: function () {
    return {
      list: [],
      keywords: "",
      page: "",
      loading: false,
      finished: false,
    };
  },
  created() {},
  mounted: function () {

  },
  methods: {
    searchData() {
      if (this.keywords != "") {
        this.page = "";
      }

    },

    getData() {

      var data = {
        search: this.keywords,
      };

      getVisiting(data).then((res) => {
        this.list = res.data.customer;

        this.finished = res.data.customer.length < 200 ? true : false;
        this.loading = res.data.customer.length < 200 ? true : false;
      });
    },
    onLoad() {
      this.getData();
    }
  },
};
</script>
<style scoped>
.container {
  background-color: #fff;
  background: #fff;
  border-radius: 5px;
}

.container .caption {
  font-weight: 600;
  height: 40px;
  line-height: 40px;
  border-bottom: 1px solid #eee;
  padding-left: 10px;

  font-size: 16px;
  color: #333;
}

.caption .month {
  height: 25px;
  width: 60px;
  background: #092e48;
  border-radius: 20px;
  color: #fff;
  line-height: 25px;
  font-size: 12px;
}

.caption .month span {
  display: inline-block;
  width: 23px;
  height: 23px; 
  background: #fff;
  border-radius: 50%;
  margin-left: 1px;
  margin-right: 4px;
}



.container .list {
  line-height: 43px;
  font-size: 14px;
}

.container .list .item {
  padding: 15px 0;
  padding-left: 8px;
  border-bottom: 10px solid #f9f9f9;
}

.container .list .item div {
  width: 100%;
}

.container .list img {
  width: 28px;
  margin-right: 10px;
}

.container .list .info {
  line-height: 18px;
  font-size: 12px;
}

.container .list .item .right {
  padding-right: 15px;
  font-weight: bold;
}

.container .list .item .right span {
  font-size: 10px;
  font-weight: 500;
}

.company {
  color: #333;
  font-size: 16px;
}

.contact {
  color: #999;
  margin: 5px 0;
  font-size: 12px;
}

.contact span {
  padding-left: 10px;
}

.address {
  color: #999;
  font-size: 12px;
  margin-bottom: 5px;
}

.list img {
  width: 30px;
}
</style>
